import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { TodoListService } from '../../../api/controllers/TodoList';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-todo-history',
  templateUrl: './todo-history.component.html',
  styleUrls: ['./todo-history.component.scss']
})
export class TodoHistoryComponent implements OnInit {

  todoListHeader: any
  todoList: any;
  gridSourceData: any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(private todoService: TodoListService, private cookieService: CookieService) { }

  ngOnInit() {
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.todoListHeader = [
      {
        text: 'todo id',
        datafield: 'todoListId',
        width: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'title',
        datafield: 'title',
        minwidth: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'description',
        datafield: 'description',
        minwidth: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'status',
        datafield: 'todoStatusId',
        minwidth: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      }, {
        text: 'category',
        datafield: 'todoListCategoryId',
        cellsrenderer: cellsrenderer,
        minwidth: 200,
        renderer: columnrenderer
      }, {
        text: 'due date',
        datafield: 'finishOn',
        cellsrenderer: cellsrenderer,
        minwidth: 200,
        renderer: columnrenderer
      }];
      this.getTodoHistory();
  }

  getTodoHistory() {
    let getTodoParam = {
      apartmentId : parseInt(this.cookieService.get('apartmentId'))
    }
    this.todoService.getAllTodoLists(getTodoParam).subscribe(
      (resp: any) => {
        var localData = resp.filter(data => {
          return data.isActive
        })
        this.gridSourceData = {
          localdata: localData,
          datatype: "array"
        }
        this.todoList = new jqx.dataAdapter(this.gridSourceData);
      },
      error => {

      }
    )
  }

}
