import { Component, OnInit, ViewChild, Injector } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { TodoListService } from '../../../api/controllers/TodoList';
import { CookieService } from 'ngx-cookie-service';
import { TodoCreateDialogComponent } from '../todo-create-dialog/todo-create-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';

@Component({
  selector: 'app-todo-list',
  templateUrl: './todo-list.component.html',
  styleUrls: ['./todo-list.component.scss']
})
export class TodoListComponent implements OnInit {
 
  public stepperList:Array<any>=[];
  gridSourceData: any;
  todoList: Array<any> = [];
  rawTodoList:Array<any>=[];
  modalService:any;
  searchTodoList:any;

  constructor(private injector: Injector,private sharedService : SharedService, private todoService: TodoListService,private cookieService:CookieService,public dialog : MatDialog ) {
    this.modalService = this.injector.get(ModalService);
  }

  ngOnInit(): void {
    this.stepperList=[
      {
        id:216,
        name:'Open',
        color:'#fa7b7b',
        isActive:false
      },
      {
        id:217,
        name:'InProgress',
        color:'#ff9800',
        isActive:false
      },
      { 
        id:218,
        name:'Completed',
        color:'#4caf50',
        isActive:false
      },
    ];
        // delete item
        this.sharedService.unitlistdeleteindexcast.subscribe(item => {
          if (item != null) {
            let deleteParam ={
              todoListId:item,
              deleteBy:parseInt(this.cookieService.get('userId'))
            }
            this.todoService.deleteTodoList(deleteParam).subscribe(
              (resp: any) => {
                this.sharedService.setAlertMessage(`Todo Deleted successfully`);
                this.sharedService.setUnitListDeleteIndex(null);
                this.getTodoList();
              },
              error => {
        
              }
            )
    
          }
    
        });
    this.getTodoList();
  }


  getTodoList() {
    let getList ={
      apartmentId:parseInt(this.cookieService.get('apartmentId')),
      staffId:1
    }
    this.todoService.getTodoListByStaffId(getList).subscribe(
      (resp: any) => {
        this.todoList = resp.filter(data => {
          return data.isActive
        });
        this.rawTodoList = resp.filter(data => {
          return data.isActive
        });
      },
      error => {

      }
    )
  }



  openEdit(id){
    let data:any={
      id:id,
      type:'edit'
    }
    const dialogRef = this.dialog.open(TodoCreateDialogComponent, {
      width: 'auto',
      height: 'auto',
      data: data
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.sharedService.setAlertMessage(`Todo Updated successfully`);
        this.getTodoList();
      }
    });
  }
  deleteList(id){
    this.modalService.showConfirmModal(id);
  }
  changeStatus(obj,category){
    let updateTodo = obj;
    if(category == 'inprogress'){
      updateTodo.todoStatusId = 217;
    }else if(category == 'completed'){
      updateTodo.todoStatusId = 218;
    }
    updateTodo.insertedBy = 0;
    updateTodo.insertedOn = new Date().toISOString();
    updateTodo.updatedOn = new Date().toISOString();
    updateTodo.updatedBy = parseInt(this.cookieService.get('userId'));
    let updateTodoParam ={
      todoListModel : updateTodo
    }
    this.todoService.updateTodoList(updateTodoParam).subscribe(
      resp =>{
        this.sharedService.setAlertMessage(`Todo Status Changed successfully`);
        this.getTodoList();
      }
    )
  }

  getPriority(id){
    if(id == 205){
      return '4px solid #f44336'
    }else if(id == 206){
      return '4px solid #FF9800'
    }else{
      return '4px solid #ffc107'
    }
  }

  filterItem(value){
    if(!value){
        this.getTodoList();
    }
    this.todoList = Object.assign([], this.rawTodoList).filter(item => item.title.toLowerCase().indexOf(value.toLowerCase()) > -1)
 }

  getStepperList(statusId){
    let selectedStepper = [];
      selectedStepper = this.stepperList.filter((key,index) =>{
        if(statusId == 216 && index < 1){
          key.isActive = true;
        }else if (statusId == 217 && index < 2){
          key.isActive = true;
        }else if(statusId == 218 && index < 3){
          key.isActive = true;
        }else{
          key.isActive = false;
        }
        return key;
      })
      return selectedStepper;
  }
}
