import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { TodoListService } from '../../../api/controllers/TodoList';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-todo-reminder',
  templateUrl: './todo-reminder.component.html',
  styleUrls: ['./todo-reminder.component.scss']
})
export class TodoReminderComponent {
  gridSourceData: any;
  todoReminderList: Array<any> = [];
  rawTodoReminderList:Array<any>=[];
  categoryList: Array<any> = [];
  searchTodoList:any;

  constructor(private todoService: TodoListService,private cookieService : CookieService) {

  }

  ngOnInit(): void {
    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.
    this.getCategory();
    this.getTodoHistory();
  }

  getCategory(){
    let getCategoryParam = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.todoService.getAllTodoListCategorysByApartmentId(getCategoryParam).subscribe(
      (resp: any) => {
        this.categoryList = resp.filter(key => {
          return key.isActive;
        })
      },
      error => {

      }
    )
  }


  getTodoHistory() {
    let getTodoParam = {
      apartmentId : parseInt(this.cookieService.get('apartmentId'))
    }
    this.todoService.getAllTodoLists(getTodoParam).subscribe(
      (resp: any) => {
        this.todoReminderList = resp.filter(data => {
          return data.isActive
        });
        this.rawTodoReminderList = resp.filter(data => {
          return data.isActive
        });
      },
      error => {

      }
    )
  }

  changeStatus(obj,category){
    let updateTodo = obj;
    if(category == 'inprogress'){
      updateTodo.todoStatusId = 217;
    }else if(category == 'completed'){
      updateTodo.todoStatusId = 218;
    }
    updateTodo.insertedBy = 0;
    updateTodo.insertedOn = new Date().toISOString();
    updateTodo.updatedOn = new Date().toISOString();
    updateTodo.updatedBy = parseInt(this.cookieService.get('userId'));
    let updateTodoParam ={
      todoListModel : updateTodo
    }
    this.todoService.updateTodoList(updateTodoParam).subscribe(
      resp =>{
        this.getTodoHistory();
      }
    )
  }

  getCategoryName(id){
    let categoryName:any;
    this.categoryList.filter(key =>{
     if(key.todoListCategoryId == id){
      categoryName = key.categoryName;
      }
    })
    return categoryName;
  }

  filterItem(value){
    if(!value){
        this.getTodoHistory();
    }
    this.todoReminderList = Object.assign([], this.rawTodoReminderList).filter(item => item.title.toLowerCase().indexOf(value.toLowerCase()) > -1)
 }

  getPriority(id){
    if(id == 205){
      return '4px solid #f44336'
    }else if(id == 206){
      return '4px solid #FF9800'
    }else{
      return '4px solid #ffc107'
    }
  }

}
